import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';

import ChartView from './ChartView';
import TableHeader from './TableHeader';
import calculate from '../math';

import '../assets/css/Table.css';

const generateRows = data => data.map(el => (el = createData(el)));

const createData = ({ id, type, r1, r2, r3, r4 }) => {
  r1 = pointsToString(r1);
  r2 = pointsToString(r2);
  r3 = r3.toFixed(5);
  r4 = pointsToString(r4);

  return { id, type, r1, r2, r3, r4 };
};

const pointsToString = r => r.reduce((str, el) => (str += `[${el.x},${el.y.toFixed(3)}]; `), '');

export default class ResultsTable extends React.Component {
  constructor({ file }) {
    super();

    this.state = {
      dotsForChart: [],
      data: calculate(file)
    };
  }

  openChart = (id = -1) => {
    if (id === -1) {
      return this.setState({ dotsForChart: this.state.data.PtSum });
    }
    this.state.data.forEach(el => {
      if (el.id === id) {
        return this.setState({ dotsForChart: el });
      }
    });
  };

  closeChart = () => this.setState({ dotsForChart: [] });

  exportTableToExcel() {
    const dataType = 'application/vnd.ms-excel';
    const tableResults1 = document.getElementById('results1');
    const tableResults2 = document.getElementById('results2');

    const tableHTML =
      tableResults1.outerHTML.replace(/ /g, '%20') +
      tableResults2.outerHTML
        .replace(/ /g, '%20')
        .replace(
          /<td%20class="MuiTableCell-root%20MuiTableCell-body%20MuiTableCell-sizeSmall"><button%20class="chart-but"><span%20class="material-icons%20MuiIcon-root"%20aria-hidden="true">show_chart<\/span><\/button><\/td>/g,
          ''
        );
    const filename = 'results.xls';
    const downloadLink = document.createElement('a');

    document.body.appendChild(downloadLink);
    if (navigator.msSaveOrOpenBlob) {
      var blob = new Blob(['\ufeff', tableHTML], {
        type: dataType
      });
      navigator.msSaveOrOpenBlob(blob, filename);
    } else {
      downloadLink.href = 'data:' + dataType + ', ' + tableHTML;
      downloadLink.download = filename;
      downloadLink.click();
    }
  }

  render() {
    const rows = generateRows(this.state.data);
    return (
      <div style={{ width: '95%' }}>
        {this.state.dotsForChart.length !== 0 && <ChartView dots={this.state.dotsForChart} close={() => this.closeChart()} />}
        <TableHeader />
        <Button variant="contained" color="primary" size="small" className="save-but" onClick={this.exportTableToExcel}>
          <Icon>save</Icon>
        </Button>
        <Paper style={{ width: '100%', overflowX: 'auto', margin: '10px 0' }}>
          <Table id="results1" style={{ minWidth: 650 }} size="small">
            <TableHead></TableHead>
            <TableBody>
              <TableRow key={0}>
                <TableCell component="th" scope="row">
                  <strong>Оценка средней наработки на отказ объекта</strong>
                </TableCell>
                <TableCell align="right">{this.state.data.TSrSum}</TableCell>
              </TableRow>
              <TableRow key={1}>
                <TableCell component="th" scope="row">
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <strong>Оценка ВБР объекта</strong>
                    <button className="chart-but" onClick={() => this.openChart()}>
                      <Icon>show_chart</Icon>
                    </button>
                  </div>
                </TableCell>
                <TableCell align="right">{pointsToString(this.state.data.PtSum)}</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </Paper>
        <Paper style={{ width: '100%', overflowX: 'auto', margin: '10px 0' }}>
          <Table id="results2" style={{ minWidth: 650 }} size="small">
            <TableHead>
              <TableRow>
                <TableCell>Номер объекта</TableCell>
                <TableCell align="right">Тип Распределения</TableCell>
                <TableCell align="center">F(t), [0, t]</TableCell>
                <TableCell align="center">f(t), [0, t]</TableCell>
                <TableCell align="center">
                  T<sub>ср</sub>
                </TableCell>
                <TableCell align="center">P(t), [0, t]</TableCell>
                <TableCell />
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map(row => (
                <TableRow key={row.id}>
                  <TableCell component="th" scope="row">
                    {row.id}
                  </TableCell>
                  <TableCell align="right">{row.type}</TableCell>
                  <TableCell align="center">{row.r1}</TableCell>
                  <TableCell align="center">{row.r2}</TableCell>
                  <TableCell align="center">{row.r3}</TableCell>
                  <TableCell align="center">{row.r4}</TableCell>
                  <TableCell>
                    <button className="chart-but" onClick={() => this.openChart(row.id)}>
                      <Icon>show_chart</Icon>
                    </button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Paper>
      </div>
    );
  }
}
