const even = x => x % 2 === 0;

const F0 = x => {
  const getFromArray = x => tableF0[Number(x.slice(0, -1))][Number(x)];

  x = x.toFixed(2);
  if (Number(x) < 0) return 0;
  if (Number(x) >= 0 && Number(x) <= 2.48) {
    if (even(x[3])) {
      return getFromArray(x);
    } else {
      const left = getFromArray((Number(x) - 0.01).toFixed(2));
      const right = getFromArray((Number(x) + 0.01).toFixed(2));
      return (left + right) / 2;
    }
  }
  if (Number(x) > 2.48) {
    return 0.99999999;
  }
};

const Pm = (x, m) => {
  const getFromArray = (x, m) => tablePM[Number(x)][m];

  const average = (x, m, left, right) => {
    if (x >= 10) {
      return (getFromArray(left, m) + getFromArray(right, m)) / 2;
    } else {
      if (typeof right === 'string') {
        const l = x.substring(0, x.length - 1) + left;
        const r = Number(right);
        return (getFromArray(l, m) + getFromArray(r, m)) / 2;
      }

      if (typeof left === 'string') {
        const l = Number(left);
        const r = x.substring(0, x.length - 1) + right;
        return (getFromArray(l, m) + getFromArray(r, m)) / 2;
      }

      const l = x.substring(0, x.length - 1) + left;
      const r = x.substring(0, x.length - 1) + right;
      return (getFromArray(l, m) + getFromArray(r, m)) / 2;
    }
  };
  let _x = x;
  x = _x.toFixed(1);
  if (x[0] == 0) return getFromArray(Number(x[0]), m);

  if (x >= 1 && x < 10) {
    if (even(x[0])) {
      if (x[2] == 0 || x[2] == 4 || x[2] == 8) return getFromArray(x, m);
      if (x[2] > 0 && x[2] < 4) return average(x, m, 0, 4);
      if (x[2] > 4 && x[2] < 8) return average(x, m, 4, 8);
      if (x[2] > 8) {
        let right = x.split('.');
        right[0] = Number(right[0]) + 1;
        right[1] = 2;
        right = right.join('.');
        return average(x, m, 8, right);
      }
    } else {
      if (x[2] == 2 || x[2] == 6) return getFromArray(x, m);
      if (x[2] > 2 && x[2] < 6) return average(x, m, 2, 6);
      if (x[2] < 2) {
        let left = x.split('.');
        left[0] = Number(left[0]) - 1;
        if (left[0] === 0) return average(x, m, '0', 2);
        left[1] = 8;
        left = left.join('.');
        return average(x, m, left, 2);
      }
      if (x[2] > 6) {
        let right = x.split('.');
        right[0] = Number(right[0]) + 1;
        return average(x, m, 6, String(right[0]));
      }
    }
  }

  x = Number(_x.toFixed(0));
  if (x >= 10 && x < 20) {
    if (even(x)) return getFromArray(x, m);
    else return average(x, m, x - 1, x + 1);
  }

  if (x >= 20 && x <= 50) {
    if (x === 20 || x === 24 || x === 28 || x === 32 || x === 50) return getFromArray(x, m);
    else {
      if (x > 20 && x < 24) return average(x, m, 20, 24);
      if (x > 24 && x < 28) return average(x, m, 24, 28);
      if (x > 28 && x < 32) return average(x, m, 28, 32);
      if (x > 32 && x < 50) return average(x, m, 32, 50);
    }
  }

  if (x > 50) return 0;
};

const G = x => {
  const p = [
    0.99999999999980993,
    676.5203681218851,
    -1259.1392167224028,
    771.32342877765313,
    -176.61502916214059,
    12.507343278686905,
    -0.13857109526572012,
    9.9843695780195716e-6,
    1.5056327351493116e-7
  ];

  let i;
  const g = 7;
  if (x < 0.5) return Math.PI / (Math.sin(Math.PI * x) * G(1 - x));
  x -= 1;
  let a = p[0];
  const t = x + g + 0.5;
  for (i = 1; i < p.length; i++) {
    a += p[i] / (x + i);
  }
  return Math.sqrt(2 * Math.PI) * Math.pow(t, x + 0.5) * Math.exp(-t) * a;
};

export { F0, Pm, G };
