import { create, all } from 'mathjs';
import { F0, Pm, G } from './tables';

const config = {};
const math = create(all, config);
math.import(require('mathjs-simple-integral'));

const average = array => {
  const sum = array.reduce((a, b) => a + b, 0);
  return sum / array.length;
};

/*-------------------------Начало расчетов оценки средней наработки на отказ объекта------------------------*/
const prepareValuesForTsrSumm = data =>
  data.reduce(
    (types, el) => {
      types[el.t].count++;
      types[el.t].Tsr.push(el.r3);
      return types;
    },
    {
      1: { count: 0, Tsr: [] },
      2: { count: 0, Tsr: [] },
      3: { count: 0, Tsr: [] },
      4: { count: 0, Tsr: [] },
      5: { count: 0, Tsr: [] },
      6: { count: 0, Tsr: [] }
    }
  );

const TSrSumm = data => {
  const elements = prepareValuesForTsrSumm(data);
  let result = 0;
  for (let i = 1; i <= 6; i++) {
    if (elements[i].count !== 0) {
      result += elements[i].count / average(elements[i].Tsr);
    }
  }
  return result ** -1;
};
/*-----------------------------Конец расчетов оценки средней наработки на отказ объекта-----------------------*/

/*------------------------------------Начало для расчетов оценки ВБР объекта----------------------------------*/
const prepareValuesForPtSumm = data => {
  data = data.reduce(
    (types, el) => {
      types[el.t].count++;
      types[el.t].Pt.push(...el.r4);
      return types;
    },
    {
      1: { count: 0, Pt: [] },
      2: { count: 0, Pt: [] },
      3: { count: 0, Pt: [] },
      4: { count: 0, Pt: [] },
      5: { count: 0, Pt: [] },
      6: { count: 0, Pt: [] }
    }
  );

  for (let key in data) {
    data[key].Pt = data[key].Pt.reduce((result, el) => {
      result[el.x] = result[el.x] || [];
      result[el.x].push(el.y);
      return result;
    }, Object.create(null));

    for (let t in data[key].Pt) {
      data[key].Pt[t] = average(data[key].Pt[t]);
    }
  }

  return data;
};

const PtSumm = (data, t) => {
  const elements = prepareValuesForPtSumm(data);
  let result = [];

  for (let i = 0; i <= t; i++) {
    let r = { x: i, y: 1 };
    for (let j = 1; j <= 6; j++) {
      if (elements[j].count !== 0) {
        r.y *= elements[j].Pt[i];
      }
    }
    result.push(r);
  }
  return result;
};
/*------------------------------------Конец расчетов оценки ВБР объекта----------------------------------*/

/** @description 1 - экспоненциальный
определяющий параметр распределения λ @param lyambda — интенсивность отказов (или параметр потока отказов), 1/час; λ > 0

Функция распределения отказов на интервале (0;t): 	F(t) = 1 – exp (- λt);
Функция плотности распределения на интервале (0;t):	 f(t) =  λexp(- λt);
Оценка средней наработки на отказ (до отказа):	Tcp = 1/ λ;
Оценка вероятности безотказной работы на наработке (0;t):	P(t) = exp(- λt);
*/
function exp(t, lyambda) {
  const result = { r1: [], r2: [], r3: 0, r4: [] };

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Ft = 1 - Math.exp(-lyambda * _t);
    result.r1.push({ x: _t, y: Ft });
  }

  for (let _t = 0; _t <= t; _t += 0.5) {
    const ft = lyambda * Math.exp(-lyambda * _t);
    result.r2.push({ x: _t, y: ft });
  }

  result.r3 = 1 / lyambda;

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Pt = Math.exp(-lyambda * _t);
    result.r4.push({ x: _t, y: Pt });
  }

  return result;
}

/** @description 2 - смеси двух экспоненциальных
определяющие параметры:  
@param lyambda1 и @param lyambda2 – интенсивности отказов (или параметры потоков отказов), 1/час, λ1 >0,  λ2 >0; 
весовые коэффициенты @param q1 и @param q2 , q1>0 , q2 >0, q1 + q2  = 1;
@param lyambda3 = q2λ1 + q1λ2

Функция распределения отказов на интервале (0;t): 
F(t) = 1 – [q1 λ2 exp (- λ1t) + q2 λ1 exp (- λ2t)] / λ3;
Функция плотности распределения на интервале (0;t):
f(t) =  q1 λ1 exp (- λ1t) + q2 λ2 exp (- λ2t);
Оценка средней наработки на отказ (до отказа):
Tcp = tλ3^2 / [t λ1 λ2 λ3 + q1 q2(λ1 — λ2) exp(-λ3t)];
Оценка вероятности безотказной работы на наработке (0;t):
P(t) = [q1 λ2 exp (- λ1t) + q2 λ1 exp (- λ2t)] / λ3;
 */
function mixExp(t, lyambda1, lyambda2, q1, q2) {
  const result = { r1: [], r2: [], r3: 0, r4: [] };

  const lyambda3 = q2 * lyambda1 + q1 * lyambda2;

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Ft = 1 - (q1 * lyambda2 * Math.exp(-lyambda1 * _t) + q2 * lyambda1 * Math.exp(-lyambda2 * _t)) / lyambda3;
    result.r1.push({ x: _t, y: Ft });
  }

  for (let _t = 0; _t <= t; _t += 0.5) {
    const ft = q1 * lyambda1 * Math.exp(-lyambda1 * _t) + q2 * lyambda2 * Math.exp(-lyambda2 * _t);
    result.r2.push({ x: _t, y: ft });
  }

  result.r3 = (t * lyambda3 ** 2) / (t * lyambda1 * lyambda2 * lyambda3 + q1 * q2 * (lyambda1 - lyambda2) * Math.exp(-lyambda3 * t));

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Pt = (q1 * lyambda2 * Math.exp(-lyambda1 * _t) + q2 * lyambda1 * Math.exp(-lyambda2 * _t)) / lyambda3;
    result.r4.push({ x: _t, y: Pt });
  }

  return result;
}

/** @description 3 - нормальный (усеченный нормальный)
определяющие параметры: 
@param a – математическое ожидание наработки до отказа, a > 0; 
ско @param s; 
характер усечения определяется через границы области значений переменной (@param t1;@param t2) 
оцениваемым коэффициентом усечения:		
@param c = {F0[(t2-a)/s] - F0[(a-t1)/s]}-1;
@param v = s/a ≤ 0,3 с=1;

Функция распределения отказов на интервале (0;t): 
		     t/a
	F(t) = ∫cF0[(1-x)/v]dx;
		    0
Функция плотности распределения на интервале (0;t):
  f(t) =  c∙exp[-(t-a)^2/2s^2]/(s√2π);
Оценка средней наработки на отказ (до отказа):
	Tcp = t/{ F0[(t-ka)/s√k]};
Оценка вероятности безотказной работы на наработке (0;t):
		        t/a
	P(t)= 1- c∫F0[(1-x)/v]dx;
		       0
*/
function normal(t, a, s, t1, t2) {
  const result = { r1: [], r2: [], r3: 0, r4: [] };

  let integral = math.integral('(1-x)/v', 'x').toString();
  integral = math.parse(integral);
  const v = s / a;
  const c = v <= 0.3 ? 1 : F0((t2 - a) / s) - F0((a - t1) / s) - 1;

  for (let _t = 0; _t <= t; _t += 0.5) {
    const up = integral.evaluate({ x: _t / a, v });
    const down = integral.evaluate({ x: 0, v });
    const integralRes = (F0(up) || 0) - (F0(down) || 0);
    const Ft = c * integralRes;
    result.r1.push({ x: _t, y: Ft });
  }

  for (let _t = 0; _t <= t; _t += 0.5) {
    const ft = (c * Math.exp(-((_t - a) ** 2) / (2 * s) ** 2)) / (s * Math.sqrt(2 * Math.PI));
    result.r2.push({ x: _t, y: ft });
  }

  var summ = 0;
  for (let k = 1; k <= t; k++) {
    summ += c * F0(((t - k * a) / s) * Math.sqrt(k));
  }
  result.r3 = t / summ;

  for (let _t = 0; _t <= t; _t += 0.5) {
    const up = integral.evaluate({ x: _t / a, v });
    const down = integral.evaluate({ x: 0, v });
    const integralRes = (F0(up) || 0) - (F0(down) || 0);
    const Pt = 1 - c * integralRes;
    result.r4.push({ x: _t, y: Pt });
  }
  return result;
}

/** @description 4 - логнормальный
 определяющие параметры: 
 @param a – математическое ожидание наработки до отказа, a>0; ско s>0; 

Функция распределения отказов на интервале (0;t): 
				                 t
	F(t) = exp[-(a+s^2/2)]∙∫F0[(a-lnx)/s]dx;
				               0
Функция плотности распределения на интервале (0;t):
	 f(t) =  1/(st√2π)exp[-(lnt-a)^2/2s^2];
Оценка средней наработки на отказ (до отказа):
	Tcp = exp(a+s^2/2);
Оценка вероятности безотказной работы на наработке (0;t):
				                     t
	P(t) = 1 — exp[-(a+s^2/2)]∙∫F0[(a-lnx)/s]dx;
                           0
 */
function logNormal(t, a, s) {
  const result = { r1: [], r2: [], r3: 0, r4: [] };

  let integral = math.integral('(a-log(x))/s', 'x').toString();
  integral = math.parse(integral);

  for (let _t = 0; _t <= t; _t += 0.5) {
    const up = integral.evaluate({ x: _t, a, s });
    const down = integral.evaluate({ x: 0, a, s });
    const integralRes = (F0(up) || 0) - (F0(down) || 0);
    const Ft = Math.exp(-(a + s ** 2 / 2)) * integralRes;
    result.r1.push({ x: _t, y: Ft });
  }

  for (let _t = 0; _t <= t; _t += 0.5) {
    const ft = (1 / (s * _t * Math.sqrt(2 * Math.PI))) * Math.exp(((-(Math.log(_t) - a)) ** 2 / 2) * s ** 2);
    result.r2.push({ x: _t, y: ft });
  }

  result.r3 = Math.exp(a + s ** 2 / 2);

  for (let _t = 0; _t <= t; _t += 0.5) {
    const up = integral.evaluate({ x: _t, a, s });
    const down = integral.evaluate({ x: 0, a, s });
    const integralRes = (F0(up) || 0) - (F0(down) || 0);
    const Pt = 1 - Math.exp(-(a + s ** 2 / 2)) * integralRes;
    result.r4.push({ x: _t, y: Pt });
  }

  return result;
}

