/** @description 6 — распределение Вейбулла
определяется через: параметр масштаба @param a>0; параметр формы @param b; параметр смещения @param c.
Переменная t > c

Функция распределения отказов на интервале (0;t): 
	F(t) =  1- exp[-(t-c)b/a];
Функция плотности распределения на интервале (0;t):
	f(t) = (b/a) (t-c)^(b-1)exp[-((t-c)^b)/a];
Оценка средней наработки на отказ (до отказа):
  Tcp = (a+c)^(1/b) Г(1+1/b);
Оценка вероятности безотказной работы на наработке (0;t):
  P(t) = exp[-(t-c)^b/a].
*/
function veibul(t, a, b, c) {
  const result = { r1: [], r2: [], r3: 0, r4: [] };

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Ft = 1 - Math.exp(-((_t - c) ** b) / a);
    result.r1.push({ x: _t, y: Ft });
  }

  for (let _t = 0; _t <= t; _t += 0.5) {
    const ft = (b / a) * (_t - c) ** (b - 1) * Math.exp(-((_t - c) ** b) / a);
    result.r2.push({ x: _t, y: ft });
  }

  result.r3 = (a + c) ** (1 / b) * G(1 + 1 / b);

  for (let _t = 0; _t <= t; _t += 0.5) {
    const Pt = Math.exp(-((_t - c) ** b) / a);
    result.r4.push({ x: _t, y: Pt });
  }

  return result;
}

export default function calculate(file) {
  const data = file.split('\n').map(el => el.split(' ').map(el => Number(el)));
  const t = data.shift();

  let result = [];

  data.forEach((el, id) => {
    const type = el.pop();
    let r = {};
    switch (type) {
      case 1:
        r = exp(t, ...el);
        r.t = 1;
        r.type = 'Экспоненциальный';
        break;

      case 2:
        r = mixExp(t, ...el);
        r.t = 2;
        r.type = 'Смеси двух экспоненциальных';
        break;

      case 3:
        r = normal(t, ...el);
        r.t = 3;
        r.type = 'Нормальный';
        break;

      case 4:
        r = logNormal(t, ...el);
        r.t = 4;
        r.type = 'Логнормальный';
        break;

      // case 5:
      //   r = gamma(t, ...el);
      //   r.t = 5;
      //   r.type = '«Гамма» распределение';
      //   break;

      case 6:
        r = veibul(t, ...el);
        r.t = 6;
        r.type = 'Распределение Вейбулла';
        break;

      default:
        break;
    }

    result.push({ id, t: r.t, type: r.type, r1: r.r1, r2: r.r2, r3: r.r3, r4: r.r4 });
  });

  result.PtSum = PtSumm(result, t);
  result.TSrSum = TSrSumm(result);

  return result;
}
