import React from 'react';
import Chart from 'chart.js';
import Icon from '@material-ui/core/Icon';
import IconButton from '@material-ui/core/IconButton';

import '../assets/css/ChartView.css';

export default class ChartView extends React.Component {
  componentDidMount() {
    const ctx = document.getElementById('chart');
    new Chart(ctx, {
      type: 'scatter',
      data: {
        datasets: [
          {
            label: 'F(t)',
            data: this.props.dots.r1 || this.props.dots,
            showLine: true,
            backgroundColor: 'rgb(130, 197, 255, 0.6)',
            borderColor: 'rgb(130, 197, 255, 0.8)'
          },
          {
            label: 'P(t)',
            data: this.props.dots.r4 || [],
            showLine: true,
            backgroundColor: 'rgb(73, 212, 173, 0.6)',
            borderColor: 'rgb(73, 212, 173, 0.8)'
          },
          {
            label: 'f(t)',
            data: this.props.dots.r2 || [],
            showLine: true,
            backgroundColor: 'rgb(253, 97, 97, 0.6)',
            borderColor: 'rgb(253, 97, 97, 0.8)'
          }
        ]
      },
      options: {
        scales: {
          xAxes: [
            {
              type: 'linear',
              position: 'bottom',
              scaleLabel: {
                display: true,
                labelString: 't'
              }
            }
          ]
        }
      }
    });
  }

  render() {
    return (
      <div className="chart-wrapper">
        <div className="closeBut-wrapper">
          <IconButton aria-label="close" className="close-but" onClick={this.props.close}>
            <Icon color="inherit">close</Icon>
          </IconButton>
        </div>
        <canvas id="chart" />
      </div>
    );
  }
}
