import React, { useState } from 'react';
import Icon from '@material-ui/core/Icon';
import Button from '@material-ui/core/Button';

export default function Instructions() {
  const [opened, tooglePanel] = useState(false);

  return (
    <div className="input-zone">
      <div className="instrunctions">
        <div className="istructions-header">
          <Button
            className="instrunctions-button"
            variant="contained"
            color="primary"
            onClick={() => tooglePanel(!opened)}
            endIcon={opened ? <Icon>expand_less</Icon> : <Icon>expand_more</Icon>}
          >
            <h3>Инструкция к входному файлу</h3>
          </Button>
        </div>
        {opened && (
          <div className="istructions-body">
            <div>1-ая строка - значение наработки (t)</div>
            <div>2-ая строка и далее входные значения расчетов для элемента через пробел </div>
            <div>(последнее значение в строке - номер выбранного типа распределения)</div>
            <br />
            <div>Типы распределений</div>
            <ol>
              <li>Экспоненциальный (λ)</li>
              <li>Смеси двух экспоненциальных (λ1, λ2, q1, q2)</li>
              <li>Нормальный (a, s, t1, t2)</li>
              <li>Логнормальный (a, s)</li>
              <li>«Гамма» распределение (λ, m)</li>
              <li>Распределение Вейбулла (a, b, c)</li>
            </ol>
            <div>Пример:</div>
            <ul>
              <li>13</li>
              <li>3 1</li>
              <li>2 12 0.6 0.4 2</li>
              <li>3 6 2 3 3</li>
              <li>2 3 4</li>
              <li>3 3 5</li>
              <li>3 4 7 6</li>
            </ul>
          </div>
        )}
      </div>
    </div>
  );
}
