import React from 'react';
import ResultsTable from './components/Table';
import Instructions from './components/Instructions';

import './assets/css/App.css';

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      file: null,
      calculate: false,
      ready: false
    };
  }

  componentDidMount() {
    const dropZone = document.getElementById('drop_zone');
    dropZone.addEventListener('dragover', e => this.handleDragOver(e), false);
    dropZone.addEventListener('drop', e => this.handleFileSelect(e), false);
  }

  handleFileSelect(evt) {
    evt.stopPropagation();
    evt.preventDefault();

    const files = evt.dataTransfer.files;
    if (files.length !== 1) {
      return alert('Введите один файл!');
    }

    document.getElementById('drop_zone').style.display = 'none';

    this.outputFileInfo(files[0]);

    const reader = new FileReader();
    reader.readAsText(files[0], 'ISO-8859-1');
    reader.onload = this.loaded.bind(this);
    reader.onerror = this.errorHandler.bind(this);
  }

  handleDragOver(evt) {
    evt.stopPropagation();
    evt.preventDefault();
    evt.dataTransfer.dropEffect = 'copy';
  }

  outputFileInfo(file) {
    const output = [
      '<div><strong>',
      file.name,
      '</strong> (',
      file.type || 'n/a',
      ') - ',
      file.size,
      ' bytes, last modified: ',
      file.lastModifiedDate.toLocaleDateString(),
      '</div>'
    ];

    document.getElementById('list').innerHTML = '<span>(для загрузки нового файла обновите страницу)</span>' + output.join('');
  }

  loaded(evt) {
    this.setState({ file: evt.target.result }, () => this.setState({ ready: true }));
  }

  errorHandler(evt) {
    if (evt.target.error.name === 'NotReadableError') {
      alert('ERROR:' + evt.target.error);
    }
  }

  calculate() {
    if (!this.state.file) {
      return alert('Файл не загружен!');
    }
    this.setState({ calculate: true });
  }

  render() {
    return (
      <div className="App">
        {!this.state.calculate ? (
          <div style={{minWidth:470}}>
            <div className="input-zone">
              <div id="drop_zone">Перетащите файл с входными данными сюда</div>
              <output id="list"></output>
              <button className="confirm" onClick={this.calculate.bind(this)}>
                рассчитать
              </button>
            </div>
            <Instructions />
          </div>
        ) : (
          <ResultsTable file={this.state.file} />
        )}
      </div>
    );
  }
}
